body{width:100vw;height:100vh;margin:0;padding:0;color:#333;background:url("../img/00-template.svg") center no-repeat;background-size:contain}.slides{width:100vw;height:100vh;position:relative;overflow:hidden}.slide{width:100vw;height:100vh;top:0;left:0;background-size:contain;background-position:center;background-repeat:no-repeat;position:absolute;transition:top .5s ease-in-out}.current{top:0}.next{top:100vh}.previous{top:-100vh}